import CONFIG from '../../globals/config';
import '../../index';

const Home = {
  async render() {
    return `
            <main id="mainContent">
                <div class="hero">
                <div class="inner">
                    <h2>
                    "Laparnya Anda Rejeki untuk Kami"
                    </h2>
                    <p>
                    Yomakan Menyediakan berbagai tempat Makan dan minum di seluruh penjuru negeri. 
                    Penasaran? Yuk langsung Explore sekarang !!
                    </p>
            
                    <button style="visibility : hidden" tabindex="0" id="tombol" class="explore-now">Explore Now!</button>
                </div>
                </div>
        
                <div id="content"></div>
                <div class="explore">
                <h3>List Resto</h3>
                </div>
                <div id="restaurants" tabindex="0">
                </div>
            </main>
        `;
  },

  async afterRender() {
    try {
      const fetchData = async () => {
        try {
          const response = await fetch('https://restaurant-api.dicoding.dev/list');
          const data = await response.json();
          return data;
        } catch (error) {
          console.error('Error:', error);
          throw error;
        }
      };

      const data = await fetchData();
      const restaurantsContainer = document.getElementById('restaurants');

      data.restaurants.forEach((restaurant) => {
        const restaurantItem = document.createElement('div');
        restaurantItem.classList.add('container');
        restaurantItem.innerHTML = `  
                    <div class="card" tabindex="0">
                        <img class="lazyload" data-src="${CONFIG.BASE_IMAGE_URL + restaurant.pictureId}" alt="${restaurant.name}" crossorigin="anonymous">
                        <div class="card-content">
                            <h2 class="title-resto">${restaurant.name}</h2>
                            <p>Rating: ${restaurant.rating}</p>
                            <p>Location: ${restaurant.city}</p>
                            <br>
                            <a class="detail-resto" href="/#/detail/${restaurant.id}">See More</a>
                        </div>
                    </div>
                `;
        restaurantsContainer.appendChild(restaurantItem);
      });
    } catch (error) {
      console.error('Error:', error);
    }
  },
};

export default Home;
