import FavoriteRestaurantsIdb from '../../data/favorite-restaurants-idb';
import '../../index';
import '../../../styles/favorite.css';
// import {
//     createMovieItemTemplate
// } from '../templates/template-creator';

const Favorite = {
  async render() {
    return `
        <div class="hero">
            <div class="inner">
                <h2>
                    "Laparnya Anda Rejeki untuk Kami"
                </h2>
                <p>
                    Yomakan Menyediakan berbagai tempat Makan dan minum di seluruh penjuru negeri.
                    Penasaran? Yuk langsung Explore sekarang !!
                </p>

                <button tabindex="0" id="tombol" class="explore-now" style="visibility : hidden">Explore Now!</button>
            </div>
        </div>
        <div class="explore">
        <h3>List Resto Favorite</h3>
        </div>
        <div id="empty_content" class="empty_content"></div>
        <div id="restaurants"></div>

    `;
  },

  async afterRender() {
    const restaurants = await FavoriteRestaurantsIdb.getAllRestaurant();
    const restaurantContainer = document.querySelector('#restaurants');
    const emptyRestaurant = document.querySelector('.empty_content');

    if (restaurants.length === 0) {
      emptyRestaurant.innerHTML = 'Belum ada Restaurant yang difavorite kan. Silahkan tambahkan resto favorite';
    }// Periksa apakah elemen #restaurants ada
    restaurants.forEach((restaurant) => {
      restaurantContainer.innerHTML += `
                    <div class="card" tabindex="0">
                        <img class="lazyload" crossorigin="anonymous" data-src="https://restaurant-api.dicoding.dev/images/large/${restaurant.pictureId}" alt="${restaurant.name}">
                        <div class="card-content">
                            <h2 class="title-resto">${restaurant.name}</h2>
                            <p>Rating: ${restaurant.rating}</p>
                            <br>
                            <a class="detail-resto" href="/#/detail/${restaurant.id}">See More</a>
                        </div>
                    </div>
                `;
    });
  },
};

export default Favorite;
